import { useEffect, useState } from "react";
import Home from "./Home";

const Board = () => {

  // url
  const url = 'http://127.0.0.1:8080/api/board';

  // 게시물 등록
  const [vo , setVo] = useState(
    {
      title : ''  ,
      content : '' ,
    }
  )
  
  // input , textarea 입력 시 , 값 변경되는 함수
  function handleChange(evt) {
    setVo({...vo , [evt.target.name] : evt.target.value});
  }
  
  // 폼 제출 함수
  function handleSubmit(evt) {
    evt.preventDefault();

    fetch(url , {
      method : 'POST' , 
      headers : {
        "Content-Type" : "application/json" ,
      } ,
      body : JSON.stringify(vo) ,
    })
    .then( resp => resp.text() )
    .then( data => {
      // 결과 1이 나올 때 출력
      if( data === '1'){
        alert("게시물 등록 됐습니다.");

        // 작성하고 바로 게시물 다시 불러오기 
        fetch(url)
        .then(resp => resp.json())
        .then(voList => {
          setVoList(voList);
        });

      } else {
        alert("게시물 등록을 실패했습니다.");
      }
    })
    .catch((err) => {
      console.log(err);
      alert("게시물 등록 중 오류가 났습니다.");
    });
  }

  // 게시물 목록 출력
  const [voList , setVoList] = useState([]);

  useEffect(() => {
    fetch(url)
      .then(resp => resp.json())
      .then(data => {
        setVoList(data);
      })
      .catch((err) => {
        console.log(err);
        alert("게시물 목록 조회 중 오류가 났습니다.");
      });
  }, []);

  // 날짜 변경 함수
  function formatDate (timeStamp) {
    return new Date(timeStamp).toLocaleDateString();
  }

  return (
    <>
      <Home/>
      <h3>게시물 작성</h3>
        <form onSubmit={handleSubmit}>
          <label>제목</label>
          <input type="text" name="title" placeholder='제목을 입력하세요.' onChange={handleChange}/>
          <br />
          <label>내용</label>
          <textarea name="content" placeholder='내용을 입력하세요.' onChange={handleChange}></textarea>
          <br />
          <input type="submit" value='작성하기' />
        </form>
      <br />
      <h3>게시물 목록</h3>
      <table border={1}>
        <thead>
          <tr>
            <th>순번</th>
            <th>제목</th>
            <th>내용</th>
            <th>작성일자</th>
          </tr>
        </thead>
        <tbody>
          {voList.map(vo => {
            return <>
              <tr key={vo.no}>
                <td>{vo.no}</td>
                <td>{vo.title}</td>
                <td>{vo.content}</td>
                <td>{formatDate(Number(vo.createAt))}</td>
              </tr>
            </>
          })}
        </tbody>
      </table>
      <br /><br />
    </>
  );
};

export default Board;