import { useState } from "react";
import Home from "../pages/Home";

const Counter = () => {

  const [num , setNum] = useState(0);

  return (
    <>
      <Home/>
      <h3>COUTER USESTATE</h3>
      <h4>NUM : {num}</h4>
      <button onClick={() => {setNum(num + 1)}}>+</button>
      <button onClick={() => {setNum(num - 1)}}>-</button>
      <br />
      <p>
        컴포넌트 내에서 상태(state)를 관리할 수 있게 해주는 Hook
      </p>
    </>
  );
};

export default Counter;