import Home from "../pages/Home";
import { useSelector , useDispatch } from 'react-redux';
import { plus, minus } from '../redux/counterSlice';

const CounterMine2 = () => {
  const cnt = useSelector(state => state.counter.cnt);
  const dispatch = useDispatch();

  return (
    <>
      <Home />
      <h3>COUNTER REDUCER</h3>
      <h4>NUM : {cnt}</h4>
      <button onClick={() => dispatch(plus())}>+</button>
      <button onClick={() => dispatch(minus())}>-</button>
      <br />
      <p>
        - Store	: 애플리케이션의 전체 상태(state)를 저장하는 객체
        <br />
        - Action : 상태를 변경하기 위한 지시서 (무슨 일이 일어났는지를 설명하는 객체)
        <br />
        - Reducer : Action을 받아서 상태(state)를 실제로 바꿔주는 순수 함수
        <br />
        - Dispatch : Action을 Reducer에게 전달하는 함수
        <br />
        - Selector : store에서 특정 state를 꺼내올 때 사용
      </p>
    </>
  );
};

export default CounterMine2;
