import { createSlice } from '@reduxjs/toolkit';

// 초기 상태 cnt : 0
const initialState = {
  cnt: 0,
};

const CounterSlice = createSlice({
  name: 'counter', // 이름
  initialState , // 초기 상태
  reducers: { // Action을 받아서 상태(state)를 실제로 바꿔줌
    plus: (state) => {
      state.cnt += 1;
    },
    minus: (state) => {
      state.cnt -= 1;
    }
  }
});

export const { plus, minus } = CounterSlice.actions;
export default CounterSlice.reducer;